<template>
	<view class="createdWallet">
		<tabTop left-icon="back"  right-text="" title="创建钱包" :open='false' />
		<view class="form">
			<view class="upload">
				<view class="upload-img" @click='upload'></view>
			</view>
			<view class="form-input-box">
				<formInput name='钱包名称' pVal='请输入钱包名称' :tipVal='nicknameError' @val='onName' :hide='nicknameHide'/>
				<formInput name='设置密码' pVal='请输入新密码' :tipVal='pwdError' type='password' @val='onPassWord' :hide='pwdHide'/>
				<formInput name='再次输入密码' pVal='再次输入密码' :tipVal='confirm_pwdError' type='password' @val='onConfirmPassword' :hide='confirm_pwdHide'/>
			</view>
			<view class="next" @click='next'>
				<nextButton/>
			</view>
		</view>
	</view>
</template>

<script>
	import tabTop from '@/components/tabTop.vue';
	import formInput from '@/components/formInput.vue';
	import nextButton from '@/components/nextButton.vue';
	import fromTest from '@/util/fromTest.js';
	export default{
		components: {tabTop,formInput,nextButton},
		mixins: [fromTest],
		methods:{
			upload(){
				uni.chooseImage({
					success:chooseImageRes => {
						console.log(chooseImageRes);
						const tempFilePaths  = chooseImageRes.tempFilePaths;
					}
				})
			},
			next(){
				if(this.nicknamePass && this.pwdPass && this.confirm_pwdPass){
					this.$store.commit('SET_FORMDATA',this.formData);
					uni.navigateTo({
						url:'../backup/backup'
					})
					
				}else{
					uni.showToast({
						title:'请完善资料！',
						icon:'none'
					})
				}
				
			}
		}
	}
</script>

<style lang="scss">
	.createdWallet{
		@include levelCen();
		@include bg();
		.form{
			@include size(650rpx,810rpx,#4B556D);
			background-color: rgba(75,85,109,.9);
			align-self:center;
			margin-top: 52rpx;
			.upload{
				height: 100rpx;
				@include cen();
				margin-top: 50rpx;
				margin-bottom: 40rpx;
				.upload-img{
					@include size(100rpx,100rpx,#2DE9B7);
					border-radius: 50%;
				}
			}
			
			.form-input-box{
				display: flex;
				flex-direction: column;
				justify-content: center;
			}
			
			.next{
				@include size(auto,66rpx,none);
				@include cen();
				margin-top: 50rpx;
			}
		}
	}
</style>
